.site-header
{
	min-height: $spacing-unit * 1.865;
	position: relative;
	background-color: #666;
}

.site-title
{
	@include relative-font-size(1.625);
	
	$line_height: $base-line-height * $base-font-size * 2.25;
	
	font-weight: 300;
	line-height: $line_height;
	letter-spacing: -1px;
	margin-bottom: 0;
	float: left;

	&,
	&:visited
	{
		color: $grey-color-dark;
	}
	
	img
	{
		max-height: $left-side-header-offset;
		padding-right: 0.2em;
		margin-left: -$left-side-header-offset; //assumes height==width, meaning it is square (otherwise this will break)
	}
	@include media-query($left-side-too-small-for-header-offset)
	{
		img
		{
			margin-left: 0;
		}
	}
}

.site-nav
{
	float: right;
	line-height: $base-line-height * $base-font-size * 2.25;

	.nav-trigger
	{
		display: none;
	}

	.menu-icon
	{
		display: none;
	}

	.page-link
	{
		color: #ccc;
		line-height: $base-line-height;

		// Gaps between nav items, but not on the last one
		&:not(:last-child)
		{
			margin-right: 20px;
		}
	}

	@include media-query($on-palm)
	{
		position: absolute;
		top: 9px;
		right: $spacing-unit / 2;
		background-color: $background-color;
		border: 1px solid $grey-color-light;
		border-radius: 5px;
		text-align: right;

		label[for="nav-trigger"]
		{
			display: block;
			float: right;
			width: 36px;
			height: 36px;
			z-index: 2;
			cursor: pointer;
		}

		.menu-icon
		{
			display: block;
			float: right;
			width: 36px;
			height: 26px;
			line-height: 0;
			padding-top: 10px;
			text-align: center;

			> svg
			{
				fill: $grey-color-dark;
			}
		}

		input ~ .trigger
		{
			clear: both;
			display: none;
		}

		input:checked ~ .trigger
		{
			display: block;
			padding-bottom: 5px;
		}

		.page-link
		{
			display: block;
			padding: 5px 10px;

			&:not(:last-child)
			{
				margin-right: 0;
			}
			margin-left: 20px;
		}
	}
}

.site-footer
{
	border-top: 1px solid $grey-color-light;
	padding: $spacing-unit 0;

	background-color: #666;

	.social-link
	{
		color: #ccc;
	}
}

.social-contact-list,
.social-showcase-list
{
	list-style: none;
	margin-left: 0;
}

.footer-col-wrapper
{
	@include relative-font-size(0.9375);
	color: $text-color;
	margin-left: -$spacing-unit / 2;
	@extend %clearfix;
}

.footer-col
{
	float: left;
	margin-bottom: $spacing-unit / 2;
	padding-left: $spacing-unit / 2;
}

.footer-col-1
{
	width: -webkit-calc(50% - (#{$spacing-unit} / 2));
	width:         calc(50% - (#{$spacing-unit} / 2));
}

.footer-col-2
{
	float: right;
}

@include media-query($on-laptop)
{
	.footer-col-1,
	{
		width: -webkit-calc(50% - (#{$spacing-unit} / 2));
		width:         calc(50% - (#{$spacing-unit} / 2));
	}

	.footer-col-2
	{
		width: -webkit-calc(100% - (#{$spacing-unit} / 2));
		width:         calc(100% - (#{$spacing-unit} / 2));
	}
}

@include media-query($on-palm)
{
	.footer-col
	{
		float: none;
		text-align: center;
		width: -webkit-calc(100% - (#{$spacing-unit} / 2));
		width:         calc(100% - (#{$spacing-unit} / 2));
	}
}


.page-content
{
	padding: $spacing-unit 0;
	flex: 1;
}

.page-heading
{
	@include relative-font-size(2);
}

.post-list-heading
{
	@include relative-font-size(1.75);
}

.post-list
{
	margin-left: 0;
	list-style: none;

	> li
	{
		margin-bottom: 5em;
		border-bottom: 2px solid #666;
		
		> h3
		{
			background-color: #000;
			clear: both;
			padding-left: 1em;
			> a
			{
				color: #fff;
			}
		}
	}
	
	.post-meta
	{
		float: right;
		clear: both;
		background-color: #666;
		padding: 0.5em;
		color: #eee;
	}
}

.post-meta
{
	font-size: $small-font-size;
	color: $grey-color;
}

.post-link
{
	display: block;
	@include relative-font-size(1.5);
}

.all-posts-link
{
	display: flex;
	justify-content: center;
	margin-bottom: 2em;
	
	> a
	{
		@include relative-font-size(1.2);
		text-align: center;
		color: #fff;
		background-color: #000;
		padding: 0 1em; 
	}
}

.rss-subscribe
{
	margin-bottom: 0;
}

.post-header
{
	margin-bottom: $spacing-unit;
}

.post-title
{
	@include relative-font-size(2.625);
	letter-spacing: -1px;
	line-height: 1;

	@include media-query($on-laptop)
	{
		@include relative-font-size(2.25);
	}
}

.post-content
{
	margin-bottom: $spacing-unit;

	h2
	{
		@include relative-font-size(2);

		@include media-query($on-laptop)
		{
			@include relative-font-size(1.75);
		}
	}

	h3
	{
		@include relative-font-size(1.625);

		@include media-query($on-laptop)
		{
			@include relative-font-size(1.375);
		}
	}

	h4
	{
		@include relative-font-size(1.25);

		@include media-query($on-laptop)
		{
			@include relative-font-size(1.125);
		}
	}
}

.author_picture
{
	border-radius: 50%;
	width: 25%;
}

.left-side-decoration
{
	float: left;
	width: -webkit-calc((100% - #{$wrapper_width_computation} )/2);
	width: calc((100% - #{$wrapper_width_computation} )/2);

	text-align: right;
	
	@include media-query($left-side-too-small)
	{
		display: none;
	}
}

.description
{
	box-shadow: 10px 10px 5px 0px
	rgba(217,217,217,0.7);
	padding-bottom: 2em;
	padding-right: 2em;
	margin-bottom: 2em;
}

.post_toc
{
	box-shadow: 10px 10px 5px 0px
	rgba(217,217,217,0.7);
	padding-right: 2em;
	margin-bottom: 2em;
}

.centered-image
{
	margin: 0 auto;
	display: block;
}
